'use client'
// components/ControlsView.tsx
import React from 'react';
import { Opts } from '../lib/util';

export default function ControlsView({ opts }: { opts: Opts }) {
  return (
    <form id="filter-form" className="filters" action="#" onSubmit={(e) => e.preventDefault()}>
      <label className="label-inline">
        价格区间：
        <div className="range-inputs">
          <input id="price-min" name="min" type="number" min={0} defaultValue={opts.minPrice} />
          <span className="sep">—</span>
          <input id="price-max" name="max" type="number" min={0} defaultValue={opts.maxPrice} />
        </div>
      </label>

      <label className="label-block">
        排序：
        <select id="sort-select" defaultValue={opts.sort}>
          <option value="default">默认</option>
          <option value="price-asc">价格从低到高</option>
          <option value="price-desc">价格从高到低</option>
        </select>
      </label>

      <label className="label-block">
        每页：
        <select id="perpage-select" defaultValue={String(opts.perPage)}>
          <option value="8">8</option>
          <option value="12">12</option>
          <option value="24">24</option>
        </select>
      </label>

      <div className="controls-actions">
        <button id="apply-btn" type="button" className="btn-primary">应用</button>
      </div>
    </form>
  );
}